import {
    ApexBarChart,
    ButtonGroup,
    Card,
    Col,
    DropdownMenu,
    DropdownToggle,
    Row,
    SectionTitle,
    Select,
} from '@doar/components';
import { StyledBody2, StyledButton, StyledChart, StyledDropdown, StyledHeader } from './style';
import depositWithdrawalChart, { defaultDepositWithdrawalChart } from 'src/pages/chartOption/depositWithdrawalChart';
import { useEffect, useState } from 'react';

import MetricsProgress from './metrics-progress';
import MetricsProgress2 from './metrics-progress2';
import { useAppSelector } from '../../../redux/hooks';
import { webMetricsChart2 } from '@doar/shared/data/dashboard-two';

interface Props {
    selectM: string;
    setSelectM: React.Dispatch<React.SetStateAction<string>>;
    financeData?: any;
}
const WebsiteMetrics = (props: Props) => {
    const { selectM, setSelectM, financeData } = props;
    const [optionDepositWithdrawalChart, setOptionDepositWithdrawalChart] =
        useState<any>(defaultDepositWithdrawalChart);

    useEffect(() => {
        getData();
    }, [financeData?.netDepositCumulative?.list]);

    const getData = async () => {
        if (financeData?.netDepositCumulative?.list) {
            const optionReturnDepositWithdrawalChart = await depositWithdrawalChart(
                financeData?.netDepositCumulative?.list
            );
            setOptionDepositWithdrawalChart(optionReturnDepositWithdrawalChart);
        }
    };
    return (
        <Card>
            <StyledHeader>
                <div>
                    <SectionTitle
                        title="입금/출금 관리"
                        desc="대체거래를 제외한 월별 사업용계좌 입/출금 내역 분석자료입니다."
                        descProps={{ fontSize: '12px' }}
                    />
                </div>
                <StyledDropdown>
                    <DropdownToggle variant="texted">
                        {selectM || '월을 선택해주세요.'} <i className="fa fa-chevron-down" />
                    </DropdownToggle>
                    <DropdownMenu>
                        <button
                            type="button"
                            className="item"
                            onClick={() => {
                                setSelectM('1월');
                            }}
                        >
                            1월
                        </button>
                        <button
                            type="button"
                            className="item"
                            onClick={() => {
                                setSelectM('2월');
                            }}
                        >
                            2월
                        </button>
                        <button
                            type="button"
                            className="item"
                            onClick={() => {
                                setSelectM('3월');
                            }}
                        >
                            3월
                        </button>
                        <button
                            type="button"
                            className="item"
                            onClick={() => {
                                setSelectM('4월');
                            }}
                        >
                            4월
                        </button>
                        <button
                            type="button"
                            className="item"
                            onClick={() => {
                                setSelectM('5월');
                            }}
                        >
                            5월
                        </button>
                        <button
                            type="button"
                            className="item"
                            onClick={() => {
                                setSelectM('6월');
                            }}
                        >
                            6월
                        </button>
                        <button
                            type="button"
                            className="item"
                            onClick={() => {
                                setSelectM('7월');
                            }}
                        >
                            7월
                        </button>
                        <button
                            type="button"
                            className="item"
                            onClick={() => {
                                setSelectM('8월');
                            }}
                        >
                            8월
                        </button>
                        <button
                            type="button"
                            className="item"
                            onClick={() => {
                                setSelectM('9월');
                            }}
                        >
                            9월
                        </button>
                        <button
                            type="button"
                            className="item"
                            onClick={() => {
                                setSelectM('10월');
                            }}
                        >
                            10월
                        </button>
                        <button
                            type="button"
                            className="item"
                            onClick={() => {
                                setSelectM('11월');
                            }}
                        >
                            11월
                        </button>
                        <button
                            type="button"
                            className="item"
                            onClick={() => {
                                setSelectM('12월');
                            }}
                        >
                            12월
                        </button>
                    </DropdownMenu>
                </StyledDropdown>
            </StyledHeader>
            <StyledBody2>
                <Row smAlignItems="end">
                    <Col lg={8} xl={9}>
                        <StyledChart>
                            <ApexBarChart
                                options={optionDepositWithdrawalChart?.options}
                                series={optionDepositWithdrawalChart?.series || []}
                                width="100%"
                                height="100%"
                            />
                        </StyledChart>
                    </Col>
                    <Col lg={4} xl={3}>
                        <Row>
                            <Col sm={6} lg={12} mt={['10px', null, null, '0px']}>
                                <MetricsProgress2
                                    title="순입금"
                                    text={selectM}
                                    number={financeData?.netDepositCumulative?.list?.deposit[parseInt(selectM, 10) - 1]}
                                />
                            </Col>
                            <Col sm={6} lg={12} mt="10px">
                                <MetricsProgress2
                                    title="순출금"
                                    text={selectM}
                                    number={
                                        financeData?.netDepositCumulative?.list?.withdrawal[parseInt(selectM, 10) - 1]
                                    }
                                />
                            </Col>
                            <Col sm={6} lg={12} mt="10px">
                                <MetricsProgress2
                                    title="입금 누적치"
                                    text={selectM}
                                    number={financeData?.netDepositCumulative?.list?.deposit[parseInt(selectM, 10) - 1]}
                                />
                            </Col>
                            <Col sm={6} lg={12} mt="10px">
                                <MetricsProgress2
                                    title="출금 누적치"
                                    text={selectM}
                                    number={
                                        financeData?.netDepositCumulative?.list?.withdrawal[parseInt(selectM, 10) - 1]
                                    }
                                />
                            </Col>
                            <Col sm={6} lg={12} mt="10px">
                                <MetricsProgress2
                                    title="번레이트(캐쉬번)"
                                    text={selectM}
                                    number={financeData?.exchangeRate?.list[parseInt(selectM, 10) - 1]}
                                />
                            </Col>

                            <Col sm={6} lg={12} mt="10px">
                                <MetricsProgress2
                                    title="런웨이(개월)"
                                    text={selectM}
                                    number={financeData?.runway?.list[parseInt(selectM, 10) - 1]}
                                />
                            </Col>
                        </Row>
                    </Col>
                </Row>
            </StyledBody2>
        </Card>
    );
};

export default WebsiteMetrics;
