import { Card, CardBody, SectionTitle, Text } from '@doar/components';
import {
    StyledBullet,
    StyledDiv,
    StyledNumb,
    StyledProgress,
    StyledProgressBar,
    StyledRate,
    StyledTD,
    StyledTable,
    StyledTitle,
} from './style';
import { useEffect, useState } from 'react';

import { HeaderStyled } from './headerStyle';
import { calculatePercentage } from 'src/utill/calculatePercentage';

interface Props {
    liquidity?: any;
}
const CustomerSatisfaction3 = (props: Props) => {
    const { liquidity } = props;

    const [minPercent, setMinPercent] = useState<number>(0);
    const [maxPercent, setMaxPercent] = useState<number>(0);
    const [liquidityValue, setLiquidityValue] = useState<number>(0);
    const [averageYn, setAverageYn] = useState<'HIGH' | 'LOW' | null>(null);
    useEffect(() => {
        calculatePercentageHandlar();
    }, [liquidity]);

    const calculatePercentageHandlar = async () => {
        if (!liquidity?.min || !liquidity?.max || !liquidity?.liquidity) {
            setLiquidityValue(0);
            setMaxPercent(0);
            setMinPercent(0);
            return;
        }

        const percent = await calculatePercentage({
            min: parseFloat(liquidity?.min),
            max: parseFloat(liquidity?.max),
            value: parseFloat(liquidity?.liquidity),
        });
        if (percent) {
            setMaxPercent(percent);
            setMinPercent(100 - percent);
            setLiquidityValue(liquidity?.liquidity);
            if (percent > 50) {
                setAverageYn('HIGH');
            } else {
                setAverageYn('LOW');
            }
        } else {
            setAverageYn(null);
            setLiquidityValue(0);
            setMaxPercent(0);
            setMinPercent(0);
        }
    };

    return (
        <Card>
            <HeaderStyled>
                <SectionTitle title="유동성지수" />
            </HeaderStyled>
            <CardBody py={['10px', '10px']}>
                <StyledDiv>
                    <StyledNumb>{liquidityValue}</StyledNumb>
                    <StyledRate>
                        {averageYn && averageYn === 'HIGH' && (
                            <Text as="span" color="pink">
                                평균보다 높음 <i className="fa fa-arrow-up" />
                            </Text>
                        )}
                        {averageYn && averageYn === 'LOW' && (
                            <Text as="span" color="primary">
                                평균보다 낮음 <i className="fa fa-arrow-down" />
                            </Text>
                        )}
                        {!averageYn && (
                            <Text as="span" color="gray">
                                -
                            </Text>
                        )}
                    </StyledRate>
                </StyledDiv>
                <StyledTitle>
                    {liquidity?.min || 0}점~{liquidity?.max || 0}점
                </StyledTitle>
                <StyledProgress>
                    <StyledProgressBar $width={maxPercent} bg={averageYn === 'HIGH' ? 'pink' : 'primary'} />
                    <StyledProgressBar $width={minPercent} bg="gray" />
                    {/* <StyledProgressBar $width={61} bg="pink" /> */}
                </StyledProgress>
                <div
                    style={{
                        display: 'flex',
                        justifyContent: 'space-between',
                        marginBottom: '15px',
                    }}
                >
                    <span>
                        <strong>{liquidity?.min || '-'}</strong>
                    </span>
                    <span>
                        <strong>|</strong>
                    </span>
                    <span>
                        <strong>{liquidity?.max || '-'}</strong>
                    </span>
                </div>
            </CardBody>
        </Card>
    );
};

export default CustomerSatisfaction3;
