import { Button, Input, Modal, ModalBody } from '@doar/components';
import { StyledClose, StyledFooter, StyledText, StyledTitle } from './style';

import { X } from 'react-feather';

interface IProps {
    show: boolean;
    contents: string;
    onClose: () => void;
    setContents: React.Dispatch<React.SetStateAction<string>>;
}

const DetailModal = ({ show, onClose, contents, setContents }: IProps) => {
    const constonCloseHandlar = () => {
        setContents('');
        onClose();
    };
    return (
        <Modal show={show} onClose={constonCloseHandlar} size="sm">
            <ModalBody p="20px">
                <StyledClose onClose={onClose}>
                    <X size={20} />
                </StyledClose>
                <StyledTitle>반환조건</StyledTitle>
                <div style={{ overflowWrap: 'break-word' }}>
                    <StyledText>{contents}</StyledText>
                </div>
            </ModalBody>
            <StyledFooter>
                <Button color="secondary" fontSize="13px" onClick={constonCloseHandlar}>
                    닫기
                </Button>
            </StyledFooter>
        </Modal>
    );
};

export default DetailModal;
