import { Card, SectionTitle, Table } from '@doar/components';
import { StyledTD, StyledTheadTR } from './style';

import DetailModal from 'src/components/apps/chat/modal-create-channel';
import { HeaderStyled } from './headerStyle';
import ModalCreateChannel from 'src/components/apps/chat/modal-create-channel';
import { useState } from 'react';

const DataTable3 = ({ financeData }: { financeData?: any }) => {
    const [show, setShow] = useState(false);
    const [contents, setContents] = useState('');
    const handleModal = () => {
        setShow(prev => !prev);
    };
    return (
        <Card>
            <HeaderStyled>
                <SectionTitle title="정부보조금" />
            </HeaderStyled>
            <div style={{ padding: '10px' }}>
                <Table bordered mt={'10px'}>
                    <thead>
                        <StyledTheadTR>
                            <th>기본정보</th>
                            <th>금액</th>
                            <th>반환조건</th>
                        </StyledTheadTR>
                    </thead>
                    <tbody>
                        {financeData?.governmentSubsidy?.map((item: any, index: number) => {
                            return (
                                <tr>
                                    <StyledTD>{item?.title || '-'}</StyledTD>
                                    <StyledTD>
                                        <strong>{item?.balance || '-'}</strong>
                                    </StyledTD>
                                    <StyledTD>
                                        {item?.detail ? (
                                            <button
                                                type="button"
                                                className="item"
                                                onClick={() => {
                                                    setContents(item?.detail);
                                                    setShow(true);
                                                }}
                                            >
                                                상세보기
                                            </button>
                                        ) : (
                                            '-'
                                        )}
                                    </StyledTD>
                                </tr>
                            );
                        })}
                    </tbody>
                    <DetailModal show={show} onClose={handleModal} contents={contents} setContents={setContents} />
                </Table>
            </div>
        </Card>
    );
};

export default DataTable3;
