import { ApexMixChart, Card, CardBody, Col, SectionTitle } from '@doar/components';
import { MouseEvent, useEffect, useState } from 'react';
import {
    StyledBullet,
    StyledCardBodyWrap,
    StyledChart,
    StyledFlex,
    StyledHeader,
    StyledItem,
    StyledItemSub,
    StyledItemTitle,
    StyledList,
    StyledListItem,
    StyledListText,
} from './style';
import bepChart, { defaultBEPchart } from 'src/pages/chartOption/bepChart';

import ApexCharts from 'apexcharts';
import { Row } from 'styled-bootstrap-grid';
import TrafficSource2 from 'src/components/dashboard-two/traffic-source2';
import { revenueChart } from '@doar/shared/data/dashboard-five';
import { useAppSelector } from '../../../redux/hooks';

const RevenueGrowth = ({ financeData }: { financeData: any }) => {
    const [optionBEPChart, setOptionBEPChart] = useState<any>(defaultBEPchart);

    useEffect(() => {
        getData();
    }, [financeData?.bep]);

    const getData = async () => {
        if (financeData?.bep) {
            const optionReturnBEPChart = await bepChart(financeData?.bep);

            setOptionBEPChart(optionReturnBEPChart);
        }
    };
    return (
        <Card>
            <StyledHeader>
                <SectionTitle title="BEP 차트" />
            </StyledHeader>
            <CardBody p={['0px', '0px']} position="relative">
                <Row>
                    <Col sm={12} md={12} lg={8} mt="10px">
                        {/* <Acquisition /> */}
                        <StyledChart>
                            <ApexMixChart
                                options={optionBEPChart?.options}
                                series={optionBEPChart?.series || []}
                                width="100%"
                                height="300"
                            />
                        </StyledChart>
                    </Col>
                    <Col sm={12} md={12} lg={4} mt="40px" pr={'50px'}>
                        {/* <Acquisition /> */}
                        <TrafficSource2 financeData={financeData} />
                    </Col>
                </Row>
            </CardBody>
        </Card>
    );
};

export default RevenueGrowth;
