const depositWithdrawalChart = async (data?: any) => {
    const series: any = [];

    if (data) {
        if (data?.deposit) {
            const deposit = await Promise.all(
                (data?.deposit || []).map((deposit: any) => {
                    const numericValue = Number(deposit.replace(/,/g, ''));
                    return isNaN(numericValue) ? 0 : numericValue; // 숫자로 변환할 수 없으면 0으로 처리
                })
            );
            const item = {
                name: '출금',
                data: deposit,
            };
            series.push(item);
        }
        if (data?.withdrawal) {
            const withdrawal = await Promise.all(
                (data?.withdrawal || []).map((withdrawal: any) => {
                    const numericValue = Number(withdrawal.replace(/,/g, ''));
                    return isNaN(numericValue) ? 0 : numericValue; // 숫자로 변환할 수 없으면 0으로 처리
                })
            );
            const item = {
                name: '입금',
                data: withdrawal,
            };
            series.push(item);
        }
    }

    //     if (data) {
    //     // 비동기적으로 순회하려면 for...of 대신 forEach나 for...in을 사용할 수 있습니다.
    //     // 여기서는 forEach를 사용합니다.
    //     await Promise.all(Array.from(data).map(async ([key, value]) => {
    //         categories.push(key);

    //         // 여기에서도 어떤 비동기 작업을 수행한다면 await를 사용하세요.
    //         const processedValue = await someAsyncFunction(value);

    //         const item = {
    //             name: key,
    //             data: processedValue,
    //         };

    //         series.push(item);
    //     }));
    // }
    const returnData = {
        options: {
            chart: {
                height: 180,
                toolbar: {
                    show: false,
                },
            },
            plotOptions: {
                bar: {
                    horizontal: false,
                    columnWidth: '75%',
                    endingShape: 'rounded',
                },
            },
            dataLabels: {
                enabled: false,
            },
            stroke: {
                show: true,
                width: 7,
                colors: ['transparent'],
            },
            xaxis: {
                categories: ['1월', '2월', '3월', '4월', '5월', '6월', '7월', '8월', '9월', '10월', '11월', '12월'],
                labels: {
                    show: true,
                },
                axisBorder: {
                    show: false,
                },
                axisTicks: {
                    show: false,
                },
            },
            yaxis: {
                // tickAmount: 6,
                forceNiceScale: true,
                labels: {
                    style: {
                        colors: ['#8392a5'],
                        fontSize: '10px',
                    },
                    formatter: function (value: any) {
                        return (value / 10000).toFixed(0) + '만원';
                    },
                },
            },
            fill: {
                opacity: 1,
            },
            colors: ['#66a4fb', '#65e0e0'],
            grid: {
                show: true,
                borderColor: '#ebeef3',
                xaxis: {
                    lines: {
                        show: false,
                    },
                },
                yaxis: {
                    lines: {
                        show: true,
                    },
                },
            },
            // responsive: [
            //     {
            //         breakpoint: 1199,
            //         options: {
            //             stroke: {
            //                 width: 3,
            //             },
            //         },
            //     },
            //     {
            //         breakpoint: 575,
            //         options: {
            //             stroke: {
            //                 width: 3,
            //             },
            //         },
            //     },
            // ],
            legend: {
                show: true, // legend를 표시합니다.
                position: 'top', // legend의 위치를 지정할 수 있습니다.
                horizontalAlign: 'right', // legend의 수평 정렬을 지정할 수 있습니다.
                onItemClick: {
                    toggleDataSeries: true, // 데이터 시리즈를 토글할 수 있도록 설정합니다.
                },
            },
        },
        series: series,
    };
    return returnData;
};

