import { Tooltip } from 'chart.js';
const leverageChart = async ({ data, selected }: { data?: any; selected: any }) => {
    let series: any = [];

    const calculatePercentage = (value: number, total: number) => {
        return (value / total) * 100;
    };
    // fixedCost: newData[1], //고정비
    //                             variableCost: newData[2], //변동비
    //                             fixedAssets: newData[3], //고정자산
    const selectMonth = parseInt(selected.match(/\d+/)[0], 10);
    // 각각의 비율 계산
    const fixedCostPercentage = calculatePercentage(
        Number(data.fixedCost[selectMonth - 1].replace(/,/g, '')),
        Number(data.fixedCost[selectMonth - 1].replace(/,/g, '')) +
            Number(data.variableCost[selectMonth - 1].replace(/,/g, '')) +
            Number(data.fixedAssets[selectMonth - 1].replace(/,/g, ''))
    );
    const variableCostPercentage = calculatePercentage(
        Number(data.variableCost[selectMonth - 1].replace(/,/g, '')),
        Number(data.fixedCost[selectMonth - 1].replace(/,/g, '')) +
            Number(data.variableCost[selectMonth - 1].replace(/,/g, '')) +
            Number(data.fixedAssets[selectMonth - 1].replace(/,/g, ''))
    );
    const fixedAssetsPercentage = calculatePercentage(
        Number(data.fixedAssets[selectMonth - 1].replace(/,/g, '')),
        Number(data.fixedCost[selectMonth - 1].replace(/,/g, '')) +
            Number(data.variableCost[selectMonth - 1].replace(/,/g, '')) +
            Number(data.fixedAssets[selectMonth - 1].replace(/,/g, ''))
    );

    series = [variableCostPercentage, fixedAssetsPercentage, fixedCostPercentage];
    const returnData = {
        options: {
            labels: ['변동비', '고정자산', '고정비'],
            dataLabels: {
                enabled: false,
            },
            legend: {
                show: true,
                // position: "bottom",
            },
            plotOptions: {
                pie: {
                    donut: {
                        size: '0%',
                    },
                },
            },
            tooltip: {
                y: {
                    formatter: function (value: any) {
                        return value.toFixed(2) + '%'; // 소수점 2자리까지 표시
                    },
                },
            },
            colors: ['#FF6B6B', '#70C1B3', '#5E72E4'],
            stroke: {
                colors: ['#fff'],
            },
        },
        series: series,
    };
    return returnData;
};

export const defaultLeverageChart = {
    options: {
        labels: ['변동비', '고정자산', '고정비'],
        dataLabels: {
            enabled: false,
        },
        legend: {
            show: true,
            // position: "bottom",
        },
        plotOptions: {
            pie: {
                donut: {
                    size: '0%',
                },
            },
        },
        colors: ['#FF6B6B', '#70C1B3', '#5E72E4'],
        stroke: {
            colors: ['#fff'],
        },
    },
    series: [],
};

export default leverageChart;
