export const calculatePercentage = ({ min, max, value }: { min: number; max: number; value: number }) => {
    if (value < min || value > max) {
        alert('잘못된 값이 표에 들어가 있습니다. 관리자에게 확인해주세요.');
        return null;
    }

    const range = max - min;
    const normalizedValue = value - min;
    const percentage = (normalizedValue / range) * 100;

    return percentage;
};
