import { useEffect, useState } from 'react';

import Content from '../layouts/content';
import Cookies from 'js-cookie';
import { Row } from '@doar/components';
import RowFive from '../containers/dashboard-two/row-five';
import RowFour from '../containers/dashboard-two/row-four';
import RowLast from '../containers/dashboard-two/row-last';
import RowOne from '../containers/dashboard-two/row-one';
import RowThree from '../containers/dashboard-two/row-three';
import RowTwo from '../containers/dashboard-two/row-two';
import RowTwo2 from 'src/containers/dashboard-two/row-two2';
import SEO from '../components/seo';
import WelcomeArea from '../containers/dashboard-two/welcome-area';
import { getFinanceData } from '../utill/google';

const DashboardTwo = () => {
    const [financeData, setFinanceData] = useState<any>();
    const [token, setToken] = useState<any>(Cookies.get('CLUST_PARTNERS_TOKEN'));
    useEffect(() => {
        getData();
    }, [token]);
    const getData = async () => {
        const tokenItem = JSON.parse(token);
        const result: any = await getFinanceData({ token: tokenItem?.sheetUUID });
        if (result?.result === 'SUCCESS') setFinanceData(result.data);

        if (result.data?.companyNm) {
            setCompanyNm(result.data?.companyNm);
        }
    };
    const [selectM, setSelectM] = useState<string>('1월');
    const [companyNm, setCompanyNm] = useState<string>('');

    return (
        <>
            <SEO />
            <Content>
                <WelcomeArea companyNm={companyNm} />
                <Row gutters={10}>
                    <RowOne financeData={financeData} selectM={selectM} setSelectM={setSelectM} />
                    <RowFour financeData={financeData} selectM={selectM} setSelectM={setSelectM} />
                    <RowTwo financeData={financeData} />
                    <RowLast financeData={financeData} />
                    <RowTwo2 financeData={financeData} />
                </Row>
            </Content>
        </>
    );
};

export default DashboardTwo;
