const bepChart = async (data?: any) => {
    const series: any = [];
    const totalRevenue: any = [];
    const costs: any = [];
    const contributionMargin: any = [];
    if (data) {
        await Promise.all(
            data?.totalRevenue?.map((item: any, index: number) => {
                const totalRevenueItem = {
                    x: item?.toString(),
                    y: isNaN(Number(item.replace(/,/g, ''))) ? 0 : Number(item.replace(/,/g, '')),
                };
                const costsItem = {
                    x: item?.toString(),
                    y: isNaN(Number(data?.costs[index].replace(/,/g, '')))
                        ? 0
                        : Number(data?.costs[index].replace(/,/g, '')),
                };
                const contributionMarginItem = {
                    x: item?.toString(),
                    y: isNaN(Number(data?.contributionMargin[index].replace(/,/g, '')))
                        ? 0
                        : Number(data?.contributionMargin[index].replace(/,/g, '')),
                };
                totalRevenue.push(totalRevenueItem);
                costs.push(costsItem);
                contributionMargin.push(contributionMarginItem);
            })
        );

        if (totalRevenue?.length > 0) series.push({ name: '총매출', data: totalRevenue });
        if (costs?.length > 0) series.push({ name: '비용', data: costs });
        if (contributionMargin?.length > 0) series.push({ name: '공헌이익', data: contributionMargin });
    }

    const returnData = {
        options: {
            chart: {
                height: 350,
                type: 'line',
                zoom: {
                    enabled: false,
                },
                toolbar: {
                    show: false,
                },
            },
            plotOptions: {
                area: {
                    fillTo: 'origin',
                },
            },
            dataLabels: {
                enabled: false,
            },
            stroke: {
                curve: 'straight',
                width: 2,
                lineCap: 'butt',
            },
            grid: {
                row: {
                    colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
                    opacity: 0.5,
                },
            },
            xaxis: {
                type: 'category',
            },
            yaxis: {
                show: true,
                decimalsInFloat: false,
            },
            colors: ['#00275e', '#0168fa'],
        },
        series,
    };
    return returnData;
};

export const defaultBEPchart = {
    options: {
        chart: {
            height: 350,
            type: 'line',
            zoom: {
                enabled: false,
            },
        },
        plotOptions: {
            area: {
                fillTo: 'origin',
            },
        },
        dataLabels: {
            enabled: false,
        },
        stroke: {
            curve: 'straight',
            width: 2,
            lineCap: 'butt',
        },
        grid: {
            row: {
                colors: ['#f3f3f3', 'transparent'],
                opacity: 0.5,
            },
        },
        xaxis: {
            type: 'category',
        },
        yaxis: {
            show: true,
            decimalsInFloat: false,
        },
        colors: ['#00275e', '#0168fa'],
    },
    series: [],
};

export default bepChart;
